body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

h1 {
  display: none;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__min {
  z-index: 0;
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite move;
  position: absolute;
  top: 25%;
  right: calc(50% - 1.5vmin);
}

.stopwatch__sec {
  z-index: 0;
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite move;
  position: absolute;
  top: 2%;
  right: calc(50% - 1.5vmin);
}

.stopwatch__circle {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: calc(50% - 2.5vmin);
  right: calc(50% - 2.5vmin);
}

@keyframes move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stopwatch--speed-up :first-child {
  animation: 600s steps(60, end) infinite move;
}

.stopwatch--speed-up :nth-child(2) {
  animation: 10s linear infinite move;
}
